'use strict';

import React from 'react';
import RaisedButton from 'material-ui/RaisedButton';
import Paper from 'material-ui/Paper';
import CategorySelectComponent from './CategorySelectComponent';
import ProficiencySelectComponent from './ProficiencySelectComponent';
import CommentInputComponent from './CommentInputComponent';

require('styles//CommentForm.css');

const styles = {
  commentBox: {
    height: 'auto',
    width: 300,
    margin: 20,
    padding: 40,
    display: 'inline-block',
    background: '#E0F7FA'
  },

  button: {
    margin: 10
  }
};

class CommentFormComponent extends React.Component {
  state = {
    category: '',
    profiencency:'',
    comment : ''
  };

  handleChange = (event) => {
    this.setState({
      comment: event.target.value
    });
  };

  saveComment = () => {
    this.setState({comment: this.state.comment});
  };

  render() {
    return (
      <div>
        <CategorySelectComponent />
        <br />
        <ProficiencySelectComponent />
        <br />
        <CommentInputComponent />
        <br/>
        <RaisedButton label="Save" onClick={this.saveComment}  primary={true} style={styles.button}/>
        <RaisedButton label="Cancel" style={styles.button}/>
        <br/>
        <Paper style={styles.commentBox} zDepth={1} >{this.state.comment}</Paper>
      </div>
    );
  }
}

CommentFormComponent.displayName = 'CommentFormComponent';

// Uncomment properties you need
// CommentFormComponent.propTypes = {};
// CommentFormComponent.defaultProps = {};

export default CommentFormComponent;



