'use strict';

import React from 'react';
import TextField from 'material-ui/TextField';

require('styles//CategorySelect.css');

class CommentInputComponent extends React.Component {
  state = {
    value: null
  };

  handleChange = (event, index, value) => this.setState({value});

  render() {
    return (
      <TextField
        hintText="Comment Field"
        floatingLabelText="Enter comment here"
        multiLine={true}
        rows={4}
        onChange={this.handleChange}
      />
    );
  }
}

CommentInputComponent.displayName = 'CommentInputComponent';

// Uncomment properties you need
// CommentInputComponent.propTypes = {};
// CommentInputComponent.defaultProps = {};

export default CommentInputComponent;



