'use strict';

import React from 'react';
import SelectField from 'material-ui/SelectField';
import MenuItem from 'material-ui/MenuItem';
import ReadIcon from 'material-ui/svg-icons/action/visibility';
import WriteIcon from 'material-ui/svg-icons/content/create';
import GroupIcon from 'material-ui/svg-icons/social/group';
import MathIcon from 'material-ui/svg-icons/social/plus-one';

require('styles//CategorySelect.css');

class CategorySelectComponent extends React.Component {
  state = {
    value: null
  };

  handleChange = (event, index, value) => this.setState({value});

  render() {
    return (
      <SelectField
        floatingLabelText="Choose category"
        value={this.state.value}
        onChange={this.handleChange}
      >
        <MenuItem value={1} primaryText="Reading" leftIcon={<ReadIcon />}/>
        <MenuItem value={2} primaryText="Writing" leftIcon={<WriteIcon />}/>
        <MenuItem value={3} primaryText="Mathematics" leftIcon={<MathIcon />}/>
        <MenuItem value={4} primaryText="Behaviour" leftIcon={<GroupIcon />}/>
      </SelectField>
    );
  }
}

CategorySelectComponent.displayName = 'CategorySelectComponent';

// Uncomment properties you need
// CategorySelectComponent.propTypes = {};
// CategorySelectComponent.defaultProps = {};

export default CategorySelectComponent;



