'use strict';

import React from 'react';
import SelectField from 'material-ui/SelectField';
import MenuItem from 'material-ui/MenuItem';
import HappyIcon from 'material-ui/svg-icons/social/sentiment-satisfied';
import NeutralIcon from 'material-ui/svg-icons/social/sentiment-neutral';
import SadIcon from 'material-ui/svg-icons/social/sentiment-dissatisfied';
import {cyan500, red400} from 'material-ui/styles/colors';

require('styles//ProficiencySelect.css');

class ProficiencySelectComponent extends React.Component {
  state = {
    value: null
  };

  handleChange = (event, index, value) => this.setState({value});

  render() {
    return (
      <SelectField
        floatingLabelText="Choose profiencency"
        value={this.state.value}
        onChange={this.handleChange}
      >
        <MenuItem value={1} primaryText="High praise" leftIcon={<HappyIcon color={cyan500}/>}/>
        <MenuItem value={2} primaryText="Normal" leftIcon={<NeutralIcon />}/>
        <MenuItem value={3} primaryText="Special" leftIcon={<SadIcon color={red400}/>}/>
      </SelectField>
    );
  }
}

ProficiencySelectComponent.displayName = 'ProficiencySelectComponent';

// Uncomment properties you need
// ProficiencySelectComponent.propTypes = {};
// ProficiencySelectComponent.defaultProps = {};

export default ProficiencySelectComponent;



